<template>
  <div class="together">
      <searchTogether></searchTogether>
      <togetherItem :itemList = "allItems" @success="success"></togetherItem>
  </div>
</template>

<script>
import { getTogetherItemList  } from '../../api/together'; 
import searchTogether from './components/searchTogether'
import togetherItem from './components/togetherItem'

export default {
    components:{
        searchTogether,
        togetherItem,
    },
    data(){
        return {
            showAddTogether: false,
            showTogetherDetail: false,
            searchForm:{
                sortType:'date',
                creator:'',
                category:"",
                ownMember: "",
                targetMember: ""
            },
            allItems: [],
            pageSetting:{
                totalPages: null,
                curPage: 1,
            }
        }
    },
    mounted(){
        this.getTogetherItemList()
    },
    methods:{
        getTogetherItemList(){
            let params = this.searchForm
            params.curPage = this.pageSetting.curPage
            getTogetherItemList(params).then(res => {
                this.allItems = res.body
            })
        },
        openAddTogether(){
            this.showAddTogether = true
        },
        openTogetherDetail(){
            this.showTogetherDetail = true
        },
        success(){
            this.getTogetherItemList()
        }
    }
}
</script>

